import React, {useContext} from 'react';
import {ActionButton, ActionGroup, Flex, Item, Text, View} from '@adobe/react-spectrum';
import {ValostratorContext} from './ValostratorContext';
import Close from '@spectrum-icons/workflow/Close';
import Undo from '@spectrum-icons/workflow/Undo';
import Erase from '@spectrum-icons/workflow/Erase';
import Changelog from './Changelog';


const renderEvent = event => {
  let toolName = event.tool;
  if (event.tool === 'agent') {
    let side = event.side === 'a' ? 'Attackers' : 'Defenders';
    toolName += ` (${event.agent} - ${event.item} - ${side})`;
  }
  return (
    <div>
      {toolName}
    </div>
  )
}

export default function History() {
  const {
    history,
    onHistoryChange,
    onHistoryClear,
    removeHistoryEvent
  } = useContext(ValostratorContext);

  let disabledHistoryKeys = !history.length ? ['undo'] : [];
  let disableClear = !history.length ? ['clear'] : [];

  return (
    <Flex direction="column" width={400}>
      <Flex>
        <ActionGroup onAction={onHistoryChange} disabledKeys={disabledHistoryKeys} flexGrow={1}>
          <Item key="undo" textValue="undo">
            <Undo />
            <Text>Undo</Text>
          </Item>
        </ActionGroup>

        <ActionGroup onAction={onHistoryClear} disabledKeys={disableClear}>
          <Item key="clear" textValue="clear">
            <Erase />
            <Text>Clear board</Text>
          </Item>
        </ActionGroup>
      </Flex>
      <Flex direction="column" flexGrow={1}>
        {
          history.map((item, i) => {
            return (
              <Flex alignItems="center" key={i}>
                <ActionButton textValue="Delete" onPress={removeHistoryEvent.bind(null, i)} isQuiet>
                  <Close />
                </ActionButton>
                {renderEvent(item)}
              </Flex>
            );
          })
        }
      </Flex>
    </Flex>
  );

  // <Flex justifyContent="end">
  //   <Changelog />
  // </Flex>
}
